import { useRef, useEffect } from "react"
import { motion, AnimatePresence, useMotionValue } from "framer-motion"
import {Center, Box, Text} from '@chakra-ui/react'


export default function LandingPage({setShow}) {
  const x = useMotionValue(0)
  const constraintsRef = useRef(null)

  useEffect(() => x.onChange(
    latest => {
      console.log(x.get())
      if (Math.abs(x.get()) > 90) {
        setShow(true)
      }

      }
    ), [])

  return (
    <>
    <Center h='100vh'>
          <Box h='300px' w='300px' borderRadius='20px' borderWidth='1px' ref={constraintsRef}>
            <Text ml='265px' mt='3px'>
              19
            </Text>
            <Center mt='30px'>
              <AnimatePresence initial={true}>
              <motion.div 
                animate={{rotate: [-5,5,-5,5,0], transition:{duration: 0.1, repeat: Infinity, repeatDelay: 1, repeatType:'reverse'}}}
                whileHover={{ scale:[0.9,1.1], transition:{duration: 0.1, repeat: Infinity, repeatDelay: 0, repeatType:'reverse'} }} 
                whileTap={{ scale:[0.8,1.2], rotate: [-5,5], transition:{duration: 0.05, repeat: Infinity, repeatDelay: 0, repeatType:'reverse'} }} 
                drag 
                dragSnapToCenter={true} 
                whileDrag={{scale:[0.7,1.3], rotate: [0,360], transition:{duration: 0.1, repeat: Infinity, repeatDelay: 0, repeatType:'reverse'}}}  
                dragConstraints={constraintsRef}
                style = {{x}}>

                <Text fontSize='100px'>
                  Ah
                </Text>
              </motion.div>
              </AnimatePresence>
            </Center>
            

            <Box marginTop='0px' ml='65px'>
              <Text>
                The element of surprise
              </Text>
            </Box>

          </Box>
        </Center>
  </>
  )
}
