import {React, useEffect, useState} from "react";
import { Text, Heading} from "@chakra-ui/layout";

function NameChange() {

    const [count, setCount] = useState(0)
    const [name, setName] = useState('a Web Developer')
    const names = ['a Web Developer','a Photographer','an AI Enthusiast', 'a Student','a Programmer']

    useEffect(() => {
        const interval = setInterval(() => {
          setCount(count+1)
          setName(names[count%names.length])
        }, 1500);
        return () => clearInterval(interval);
      },);


    return(
        <>
        <Heading align='center'> 
            Hey, I'm Wei Kiat
        </Heading>
        <Text fontSize='24px' align='center' px='0px'>
                 {name}. <p></p><p></p> I can help <strong>YOU</strong> design solutions using technology
        </Text>
        </>
    )
}

export default NameChange