import { Container, Grid, GridItem, Heading, Text, Flex} from '@chakra-ui/react'
import {React} from 'react';
import SimpleWordCloud from './SimpleWordCloud'
import SkillBar from 'react-skillbars';

const skills = [
    {
        'type': 'AI/ML',
        'level': 75
    },
    {
        'type': 'Frontend',
        'level': 70
    },
    {
        'type': 'Backend',
        'level': 30
    },
]

const colors = {
    "bar": {
      "hue": 192,
      "saturation": 95,
      "level": {
        "minimum": 65,
        "maximum": 90
      }
    },
    "title":{
        "background":"#059CC1"
        }
}


function Skills() {
    return(
        <Container my='10px' maxW='full' h='70vh' p='0px'>
            <Grid mt='10px' h="100vh" templateRows="repeat(2, 1fr)" templateColumns="repeat(12, 1fr)" gap={1}>
            <GridItem overflow='auto' colStart={8} colSpan={5} rowStart={1} rowSpan={1} maxH='50vh'>
                <Heading> Skills </Heading>
                <Text>
                    Over the course of my studies, I have picked up various languages while working on projects involving artificial intelligence, website deployments and data management.
                </Text>
            </GridItem>
            <GridItem rowStart={1} rowSpan={1} colStart={1} colSpan={7} maxH='50vh'>
                <SimpleWordCloud />
            </GridItem>
            <GridItem rowStart={2} rowSpan={1} colStart={1} colSpan={12}>
                <Text my='5px'>Artificial Intelligence</Text>
                <div class="container">
                  <div class="skills ai" />
                </div>
                <Text my='5px' >Website Development</Text>
                <div class="container">
                    <div class="skills frontend" />
                </div>
                <Text my='5px'>Data Management</Text>
                <div class="container">
                    <div class="skills data" />
                </div>
            </GridItem>
            </Grid>
        </Container>
    )
}

export default Skills