import {React, useState} from 'react';
import ReactWordcloud from 'react-wordcloud';

 
function SimpleWordCloud() {
  const options = {
    rotations: 10,
    rotationAngles: [-30, 30],
    colors:['#30D1FA','#17CCF9','#06C2F2','#06AFD9','#059CC1'],
    enableTooltip:false,
    fontSizes:[25,40],
    padding:1,
    transitionDuration:1000,
    };

  const [hover, setHover] = useState(false)

  const callbacks = {
    onWordMouseOver:setHover,
  }

  const words = [
    {
      text: 'React',
      value: 5,
    },
    {
      text: 'NodeJS',
      value: 3,
    },
    {
      text: 'HTML',
      value: 3,
    },
    {
      text: 'CSS',
      value: 3,
    },
    {
      text: 'Tensorflow',
      value: 3,
    },
    {
      text: 'JavaScript',
      value: 5,
    },
    {
      text: 'Python',
      value: 5,
    },
    {
      text: 'MongoDB',
      value: 3,
    },
    {
      text: 'SQL',
      value: 3,
    },
    {
      text: 'Octave',
      value: 3,
    },
    {
      text: 'MatPlotLib',
      value: 3,
    },
    {
      text: 'Seaborn',
      value: 3,
    },
    {
      text: 'GAN',
      value: 3,
    },
    {
      text: 'NLP',
      value: 3,
    },
    {
      text: 'C++',
      value: 3,
    },
    {
      text: 'Stata',
      value: 3,
    }
  ]
 return <ReactWordcloud
      callbacks={callbacks}
      options={options}
      words={words}
    />}

export default SimpleWordCloud