import React from 'react'
import {Chrono} from 'react-chrono'
import {Box, Heading, Text, Wrap, WrapItem} from '@chakra-ui/react'


function About(){

    const items = [{
            title: "2015",
            cardTitle: "Attended Hwa Chong Institution",
            cardDetailedText: "Attained Hwa Chong Diploma with Distinction"
        },
        {
            title: "2019",
            cardTitle: "DSO National Laboratories",
            cardDetailedText: "Intern under Young Defence Scientist Programme"
        },
        {
            title: "2019",
            cardTitle: "New Town Primary School",
            cardDetailedText: "Provided free tuition for students at CareHut, a school-based student care centre",
        },
        {
            title: "2021",
            cardTitle: "Ministry of Health",
            cardDetailedText: "Data Coordinator at Data Fusion Center"
        },
        {
            title: "2023",
            cardTitle: "DSTA Scholarship",
        },
        {
            title: "2023",
            cardTitle: "National University Singapore",
            cardDetailedText: "Bachelors in Computer Science"
        },
      ]

    const theme = { 
        primary: "#636363",
        secondary: "#FFf",
        cardBgColor: "rgba(256,256,256,1)",
        cardForeColor: "#047591",
        titleColor: "#047591"
      }


    return(
            <Wrap w='full' h='100vh'>  
                <WrapItem overflow='auto' maxH='35vh' w='600px'>
                    <Box>
                        <Heading >About me</Heading>
                        <Text>
                            I am a programmer currently waiting for university. Intending to pursue a Bachelors
                            in Computer Sciences at NUS, I hope to leverage infocomm technologies to 
                            help improve lives. During my
                            leisure time, I enjoy photography and reading novels.
                        </Text>
                    </Box>
                </WrapItem>
                <WrapItem maxH='65vh' w='600px'>
                    <Chrono items={items} useReadMore={false} theme = {theme} mode='VERTICAL_ALTERNATING' hideControls={true} cardHeight  />
                </WrapItem>

            </Wrap>
    )
}

export default About